import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { columns, errorCols, errorOptions } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { Formik, Form, ErrorMessage } from 'formik'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import * as Yup from 'yup'
import Swal from 'sweetalert2'
import { Plus, Minus } from 'mdi-material-ui';
import {
  Grid, TextField, Fab
} from '@material-ui/core';
import { Autocomplete } from "@material-ui/lab"
import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';
import { NavLink } from "react-router-dom/cjs/react-router-dom";
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader
import authService from "../../../utils/services/auth";



//let cadeiras = []
let listaCadeira = []
let cadeirasDpt = []
let usuario = null
let dpt = null
let departamentos = []
let cursosSeleccionados = [];
export default function MuiDatatableExample() {
  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });

  const [, setIsFormValid] = useState(false);
  const [, setCade] = useState("");
  const [open, setOpen] = React.useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const [salvando, setSalvando] = useState(true);
  const [carregando, setCarregando] = useState(true);
  const [cadeiras, setCadeiras] = useState([]);


  useEffect(() => {
    cursosSeleccionados = [];
    usuario = authService.getCurrentUser();
    setTimeout(() => {
      setCarregando(false);
    }, 5000);
    usuario.authorities[0] === 'ADMIN' ? init() : usuarioLagado();
    init();

  }, [])

  async function init() {
    setCarregando(true)
    const [listacadeiras, listadepartamento, listaCursos] = await Promise.all([
      requesicoes().buscarCadeiras('?campos=id,cargaHoraria,codigo,nome,departamento.id,departamento.nome'),
      requesicoes().buscarDepartamentos('?campos=id,nome'), requesicoes().buscarCurso('?campos=id,nome')]);
    departamentos = listadepartamento.data
    listaCadeira = listacadeiras.data

    if (usuario.authorities[0] === 'DPT') {
      const cadeirasDpt = listacadeiras.data.filter(c => c.departamento.id === dpt.dpt.id).map((c, i) => ({ ...c, NO: i + 1, departamento: c.departamento.nome }))//
      setCadeiras(cadeirasDpt)
    } else {
      const cadeirasAdm = listacadeiras.data.map((c, i) => ({ ...c, NO: i + 1, departamento: c.departamento.nome }))
      setCadeiras(cadeirasAdm)
    }

    setUpdateDom(!updateDom);
    setCarregando(false)
  }
  async function usuarioLagado() {
    setCarregando(true);
    const usuarioDpt = await requesicoes().getUsuarioLogado(usuario.sub, '?campos=id,numero')
    dpt = usuarioDpt.data
    setCarregando(false)
    setUpdateDom(!updateDom);
  }
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  async function getCadeirasByDeptId(v) {
    cadeirasDpt = listaCadeira.filter(c => c.departamento.id == v.id)
    setCade(cadeirasDpt)
  }

  const options = {
    // filterType: "checkbox",
    responsive: "standard",
    elevation: 10,
    print: true,
    selectToolbarPlacement: "true",
    selectableRowsOnClick: true,
    expandableRows: false,
    expandableRowsHeader: false,
    isRowExpandable: (dataIndex, expandedRows) => {
      const { errors } = cadeiras[dataIndex];
      return errors !== null && errors !== undefined;
    },
    onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
      console.log(curExpanded, allExpanded, rowsExpanded),
    renderExpandableRow: (rowData, rowMeta) => {

      return (
        <div
          style={{
            marginLeft: "90%",
            width: "100%",
            marginTop: "3%",
            marginBottom: "3%"
          }}
        >
          <MUIDataTable
            columns={errorCols}
            cadeiras={cadeiras[5]}
            options={errorOptions}
          />
        </div>
      );
    },

    customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
      console.log(selectedRows, displayData);
      return <div> am youre toolbar header </div>;
    },
    nextPath(path) {
      this.props.history.push(path);
    },
    customToolbar: () => {

      return (
        <div>
          {usuario.authorities[0] === 'DPT' ? '' :
            <div>
              {cadeiras.length !== 0 ? <Fab variant="extended" size="medium"
                style={{
                  position: 'fixed',
                  bottom: '60px', zIndex: 500,
                  right: '15px'
                }} color="primary" onClick={handleClickOpen} >Adicionar</Fab>
                :
                <Fab variant="extended" size="medium" to="/carregar/cadeira" component={NavLink}
                  style={{
                    position: 'fixed',
                    bottom: '60px', zIndex: 500,
                    right: '15px'
                  }} color="primary" onClick={handleClickOpen} >Carregar</Fab>
              }</div>
          }
        </div>

      );
    },
    onRowSelectionChange: (
      currentRowsSelected,
      allRowsSelected,
      rowsSelected
    ) => {
      if (
        allRowsSelected !== null &&
        allRowsSelected !== undefined &&
        allRowsSelected.length > 0
      ) {
        setIsFormValid(true);
      } else {
        setIsFormValid(false);
      }
      console.log(currentRowsSelected, allRowsSelected, rowsSelected);
      rowsSelected.map((item) => console.log(cadeiras[item]));
    }
  };
  const validationSchema = Yup.object().shape({
    nome: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Campo Obrigatorio"),
  })

  const move = {
    adicionarCadeira: value => {
      cursosSeleccionados.push(value);
      setUpdateDom(!updateDom);
    },
    removerCadeira: value => {
      cursosSeleccionados = cursosSeleccionados.filter(x => x !== value);
      setUpdateDom(!updateDom);
    }
  };

  return (
    <div>
      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) : (
        <div>
          <MuiThemeProvider theme={getMuiTheme()}>
            <MUIDataTable
              title={'Lista de Disciplinas'}
              columns={columns(cadeiras)}
              data={cadeiras}
              options={options}
            // components={components}
            />
          </MuiThemeProvider>

          <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">Cadastrar Disciplina</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Preencha os dados da disciplina que pretende cadastrar
              </DialogContentText>
              <Formik initialValues={{}} validationSchema={validationSchema}

                onSubmit={(values, { setSubmitting }) => {
                  async function save(values) {
                    // values.semestres=anoFrequencia
                    try {

                      await Promise.resolve(requesicoes().postCadeira([values]));
                      //setOpen(true)
                      setTimeout(() => {
                        setSalvando(false);
                      }, 2000);
                      {
                        salvando ? (
                          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
                            <MoonLoader color="#36d7b7" />
                          </div>

                        ) : (
                          Swal.fire(
                            'Exito!',
                            'Cadeira cadastrada com sucesso!',
                            'success'
                          )
                        )
                      }

                      init();

                    } catch (error) {
                      if (error.response.status >= 500) {
                        Swal.fire({
                          icon: 'error',
                          title: 'Oops...',
                          text: error.response.data.message,
                          footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                        })
                      } else if (error.response.status < 500) {
                        Swal.fire({
                          icon: 'warning',
                          title: 'Oops...',
                          text: error.response.data.message,
                          footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                        })
                      }


                    }
                  }
                  save(values);
                  setTimeout(() => {
                    setSubmitting(false);
                  }, 500);
                }}>
                {(props) => {
                  const { isSubmitting, setFieldValue, values } = props;
                  return (
                    <Form>
                      <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                          <Input name="nome"
                            label="Nome"
                            placeholder="Nome da disciplina"
                            helperText={<ErrorMessage name='nome' />}
                            error={props.errors.nome && props.touched.nome} />

                        </Grid>

                        <Grid item xs={12} sm={3}>
                          <Input
                            fullWidth
                            label="Cdigo"
                            name="codigo"
                            placeholder="Cdigo da disciplina"
                            helperText={<ErrorMessage name='codigo' />}
                            error={props.errors.codigo && props.touched.codigo}

                          />
                        </Grid>

                        <Grid item xs={12} sm={3}>
                          <Input
                            fullWidth
                            label="Carga Horaria"
                            name="cargaHoraria"
                            placeholder="Digite"
                            helperText={<ErrorMessage name='cargaHoraria' />}
                            error={props.errors.cargaHoraria && props.touched.cargaHoraria}

                          />
                        </Grid>

                        <Grid item xs={12} sm={6} >
                          <Autocomplete onChange={(e, v) => {
                            setFieldValue('departamento', v);
                            getCadeirasByDeptId(v)
                          }}
                            fullWidth name="departamento"
                            getOptionLabel={option => option.nome}
                            options={departamentos} style={{ minWidth: 200 }}
                            renderInput={(params) => <TextField variant="outlined" margin="dense" label="Departamento"
                              fullWidth {...params} placeholder="Seleccione uma opo" />}
                          />

                        </Grid>
                        <Grid item xs={12} sm={6} >
                          <Autocomplete onChange={(e, v) => { setFieldValue('cadeirasPrecendentes', v); }}
                            fullWidth multiple name="cadeirasPrecendentes"
                            getOptionLabel={option => option.nome}
                            options={cadeirasDpt} style={{ minWidth: 200 }}
                            renderInput={(params) => <TextField variant="outlined" margin="dense" label="Cadeiras Precedentes (Se tiver uma precedencia)"
                              fullWidth {...params} placeholder="Seleccione uma opo" />}
                          />

                        </Grid>
                      </Grid>
                      <DialogActions>
                        <Button onClick={handleClose}>Cancelar</Button>
                        <Button type="submit" onClick={handleClose}>Salvar</Button>
                      </DialogActions>
                    </Form>)
                }}
              </Formik>
            </DialogContent>


          </Dialog>



        </div>
      )}
    </div>


  );
}
